/*
* Global setting and mixin
*/

@color-primary: #3ba3fd;
@color-navbar-active: #0189C3;
@color-active: #2e8ddf;
@color-background: #F3F3F3;

.border-radius(@radius) {
  -webkit-border-radius: @radius;
  -moz-border-radius: @radius;
  border-radius: @radius;
}

/**
* Global styles
*/
* {
  margin: 0px;
  padding: 0px;
  list-style-type: none;
  font-family: FontAwesome, "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  background: @color-background;
}

a {
  text-decoration: none;
}

/* header */
.header{
  padding-top: 5px;
  background: @color-background;

  .logo {
    margin: 0px;
    padding: 0;

    img {
      height: 80px;
    }
  }
  .user {
    margin-right: 20px;

    i {
      height: 80px;
      line-height: 80px;
      padding-right: 5px;
    }
  }
}

/* navbar */
.topmenu {
    background: @color-primary;
}

.menu li {
    margin: 0px;
    padding: 0px;
    position: relative;
    background: @color-primary;
}

.menu li a:hover, .menu li a:focus, .menu .active, .menu .open>a, .nav .open>a:hover, .nav .open>a:focus {
    background-color: @color-navbar-active;
}

.menu li a {
    margin: 0px;
    padding: 10px;
    min-width: 120px;
    font-size: 14px;
    line-height: 37px;
    -moz-border-radius: 0;
    -webkit-border-radius: 0;
    border-radius: 0;
    color: #FFFFFF;
    text-align: center;
}

.dropdown-menu {
	padding: 0;
}

/* banner */
.banner {
    text-align: center;
    height: 150px;
}

.banner img{
	height: 150px;
	width: 100%;
}

/*
* login-dialog
*/
#login-dialog {
  display: none;
  width: 320px;
  height: 300px;
  background-color: #fff;
  border-radius: 6px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -150px 0 0 -160px;
  z-index: 100;
}

#login-dialog .login-title{
	height: 60px;
    line-height: 60px;
    padding-left: 25px;
    border-bottom: 1px solid #e6e6e6;
    color: #333;
    font-size: 19px;
}

.login-form {
	padding: 30px;
}

.login-form input {
    width: 260px;
    height: 40px;
    line-height: 40px;
    border: 1px solid #e6e6e6;
    padding-left: 40px;
    position: relative;
}

