}

.login-form #userPsw{
	background: url(/r/cms/www/zygw/img/usernameback.png) no-repeat 10px center;
}*/

.login-form .wrongtip{
    color: #ff6767;
    font-size: 14px;
    height: 30px;
    line-height: 30px;
}

.login-form .login-btn {
    width: 260px;
    height: 40px;
    color: #fff;
    margin-top: 10px;
    background-color: @color-primary;
    font-size: 16px;
}

.login-form .login-btn:hover {
    background-color: #2e8ddf;
}

#mask{
  display: none;
  position:absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  background:#000;
  opacity:0.7;
}

/* content-list-group */
.menu-list {
    margin-left: 15px; 
    padding: 10px;
    background: #fff;
}